.sm_container {

    width: 10%;
    height: 0.5vh;
    display: flex;
    float: left;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2%;
    margin-bottom: 2%;
    gap: 4%;
}


div .heading {

    width: 15vw;
    font-weight: 200;
    font-size: 1.5vw;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

div .name {

    width: 20vw;
    font-size: 1.6vw;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    text-align: center;
}

div .pic {

    width: 3vw;
    display: flex;
    align-items: center;
    padding: 20% 0;
}

div .pic:hover {

    cursor: pointer;
}

div .copy {

    width: 10vw;
}

.copy {

    background-color: lightblue;
    animation: outHover;
    animation-duration: 1s;
    border-radius: 50px;
    height: 4vh;
}

.copy:hover {

    animation-duration: 0.3s;
    animation-name: inHover;
    animation-fill-mode: forwards;
    
}

@keyframes inHover {

    to {scale: 1.1; background-color: rgb(4, 65, 120);}
}

@keyframes outHover {

    to {scale: 1;}
    
}

@media (max-width: 1024px) {

    div .heading {

        display: none;
    }

    div .name {

        font-size: 2vw;
    }

    div .copy {

        width: 15vw;
    }
}

@media (max-width: 768px) {

    .sm_container {

        width: 20%;
        height: 5vh;
        gap: 2%;
    }

    div .pic {

        width: 7vw;
    }

}




